import React, { Component } from 'react';

class About extends Component {
  constructor(props) {
    super(props);
    this.currentInterest = 0;
    this.interests = [{image: 'images/cat.png', text: 'cats'}, {image: 'images/art.png', text: 'digital art'}, {image: 'images/martialarts.png', text: 'martial arts'}, {image: 'images/videogames.png', text: 'video games'}];
    this.interestTimer = setInterval(() => {
      this.interestClick();
    }, 5000);
  }

  interestClick = () => {
    if (this.currentInterest >= this.interests.length - 1) {
      this.currentInterest = 0;
    } else {
      this.currentInterest += 1;
    }

    clearInterval(this.interestTimer);
    this.interestTimer = setInterval(() => {
      this.interestClick();
    }, 5000);

    this.setState({state: this.state});
  };

  render() {
    return (
      <div className='about'>
        <div id='welcome'>
          <div id='name'>
            <p>RYAN</p>
          </div>

          <div>
            <img id='crown' src={'images/logo.png'} alt='logo'></img>
            <p id='royalName'>ROYAL</p>
          </div>

          <div id='name'>
            <p>TONG</p>
          </div>
        </div>

        <div id='interests'>
          <div id='header'>
            <p>INTERESTS</p>
          </div>

          <div id='interestsDisplay'>
            <img src={this.interests[this.currentInterest].image} alt='interest' onClick={this.interestClick}></img>
            <h2 onClick={this.interestClick}>{this.interests[this.currentInterest].text}</h2>
          </div>
        </div>

        <div id='projects'>
          <div id='header'>
            <p>PROJECTS</p>
          </div>

          <div id='projectsDisplay'>
            <a href='https://ucdwingchun.club' id='proj1Img' target='_blank' rel='noopener noreferrer'><img src={'images/ucdwingchun.png'} alt='ucdwingchun'></img></a>
            <div id='proj1Txt'>
              <h2>UCD Wing Chun</h2>
              <p>Website for UC Davis Wing Chun Club.</p>
            </div>

            <a href='https://walkincloset.github.io' id='proj2Img' target='_blank' rel='noopener noreferrer'><img src={'images/walkincloset.png'} alt='walkincloset'></img></a>
            <div id='proj2Txt'>
              <h2>The Walk-in Closet</h2>
              <p>Informational page for a Discord group.</p>
            </div>

            <a href='https://rrtongfirstwebsite.github.io' id='proj3Img' target='_blank' rel='noopener noreferrer'><img src={'images/rrtongfirstwebsite.png'} alt='rrtongfirstwebsite'></img></a>
            <div id='proj3Txt'>
              <h2>My First Personal Website</h2>
              <p>My personal website made during my years at UC Davis.</p>
            </div>
          </div>
        </div>

        {/* <div id='timelime'>
          <p>ahs</p>
        </div> */}
      </div>
    );
  }
}

export default About;