import React, { Component } from 'react';

class Header extends Component {
  constructor(props) {
    super(props);
    this.logoImage = 'images/cli.png';
  }

  changeLogo = () => {
    (this.logoImage === 'images/cli.png' ? this.logoImage = 'images/logo.png' : this.logoImage = 'images/cli.png');    
    this.props.handleAbout();
    this.setState({state: this.state});
  }

  render() {
    return (
      <header>
        <div className='logo'>
          <img className={'navImg'} src={this.logoImage} alt='logo' onClick={this.changeLogo}></img>
        </div>

        <nav className='nav'>
          <ul>
            <li className='first'>
              <a href='https://www.linkedin.com/in/rrtong' target='_blank' rel='noopener noreferrer'><img className={'navImg'} src={'images/linkedinW.png'} alt='linkedin' onMouseOver={e => (e.currentTarget.src = 'images/linkedin.png')} onMouseOut={e => (e.currentTarget.src = 'images/linkedinW.png')}></img></a>
            </li>
            <li>
              <a href='https://github.com/rrtong' target='_blank' rel='noopener noreferrer'><img className={'navImg'} src={'images/githubW.png'} alt='github' onMouseOver={e => (e.currentTarget.src = 'images/githubFilled.png')} onMouseOut={e => (e.currentTarget.src = 'images/githubW.png')}></img></a>
            </li>
            <li className='last'>
              <a href={'files/rrtong_resume.pdf'} target='_blank' rel='noopener noreferrer'><img className={'navImg'} src={'images/resumeW.png'} alt='resume' onMouseOver={e => (e.currentTarget.src = 'images/resume.png')} onMouseOut={e => (e.currentTarget.src = 'images/resumeW.png')}></img></a>
            </li>
          </ul>
        </nav>
      </header>
    );
  }
}

export default Header;